import React from "react";
import styled from "styled-components";
import { baseStyles } from "../../../styles/base-styles";
import { idToUrlHashFormat, scrollToHeaderElement } from "../../../utils";

const Li = styled.li`
  padding-left: ${({ level }) => level * 20}px;
`;

const Button = styled.button`
  all: unset;
  ${baseStyles()}

  position: relative;
  width: 100%;
  padding: 10px;

  border-left: ${({ level, theme }) =>
    level > 0 ? `2px solid ${theme.colors.grayBorder}` : ``};

  cursor: pointer;

  color: ${({ theme }) => theme.colors.textColor};

  background-color: ${({ isCurrentPage, theme }) =>
    isCurrentPage
      ? theme.colors.minimumHighlight
      : theme.colors.backgroundColor};

  &:hover {
    background-color: ${({ theme }) => theme.colors.minimumHighlight};
    border-left: ${({ level, theme }) =>
      level > 0 ? `2px solid ${theme.colors.minimumHighlight}` : ``};

    & > div {
      background: ${({ theme }) =>
        "linear-gradient(to right bottom, " +
        theme.colors.highlightColor +
        " 50%, " +
        theme.colors.backgroundColor +
        " 50%);"};

      border-top: 1px solid ${({ theme }) => theme.colors.grayBorder};
      border-left: 1px solid ${({ theme }) => theme.colors.grayBorder};
    }
  }
`;

const DiagonalBox = styled.div`
  position: absolute;
  bottom: 0px;
  right: 0px;

  height: 12px;
  width: 12px;
`;

export const HeaderNavigatorButton = ({
  element,
  rootHeaderLevel,
  isCurrentPositionList,
  idx,
}) => {
  const { tagName, id, innerText } = element;

  if (!["H1", "H2", "H3", "H4"].includes(tagName)) return;

  const handleOnClick = (headerId) => {
    window.location.hash = idToUrlHashFormat(headerId);
    scrollToHeaderElement(`#${headerId}`);
  };

  const level = parseInt(tagName.substring(1)) - rootHeaderLevel;

  return (
    <Li level={level}>
      <Button
        level={level}
        isCurrentPage={isCurrentPositionList[idx]}
        onClick={() => handleOnClick(id)}
      >
        {innerText}
        <DiagonalBox />
      </Button>
    </Li>
  );
};
