import React from "react";
import { graphql } from "gatsby";
import { GatsbyImage, getImage, getSrc } from "gatsby-plugin-image";
import { BLOG_POST_SECTION_CLASS } from "../constants";
import { ContentSection } from "../components/blog/content-section";
import { BlogWrapper } from "../components/blog/blog-wrapper";
import { HeaderNavigator } from "../components/blog/header-navigator";
import { useHashForInitialPosition } from "../hooks";
import SeoHelmet from "../components/seo";

const BlogPost = ({ data }) => {
  const {
    mdx: {
      frontmatter: {
        title,
        description,
        date,
        hero_image,
        hero_image_alt: imageAlt,
        category,
        tags,
      },
      parent: { modifiedTime },
      body,
      slug,
    },
  } = data;
  const image = getImage(hero_image); // == hero_image.childImageSharp.gatsbyImageData
  const mainImageSrc = getSrc(hero_image); // == hero_image.childImageSharp.gatsbyImageData.images.fallback.src;

  useHashForInitialPosition();

  const articleInfoProps = {
    published_time: date,
    modified_time: modifiedTime,
    category: category,
    tags: tags,
    mainImageAlt: imageAlt,
  };

  return (
    <>
      {/* <SeoHelmet
        title={title}
        description={description}
        mainImageSrc={mainImageSrc}
        articleInfo={articleInfoProps}
        pathname={`/${slug}`}
      /> */}
      <GatsbyImage image={image} alt={imageAlt} />
      <BlogWrapper className={BLOG_POST_SECTION_CLASS}>
        <ContentSection title={title} date={date} tags={tags} body={body} />
        <HeaderNavigator />
      </BlogWrapper>
    </>
  );
};

export const query = graphql`
  query ($id: String) {
    mdx(id: { eq: $id }) {
      body
      frontmatter {
        title
        description
        date(formatString: "YYYY-MM-DD")
        hero_image_alt
        hero_image {
          childImageSharp {
            gatsbyImageData(width: 1200, aspectRatio: 2)
          }
        }
        category
        tags
      }
      slug
      parent {
        ... on File {
          modifiedTime(formatString: "YYYY-MM-DD")
        }
      }
    }
  }
`;

export default BlogPost;
