import React from "react";
import styled from "styled-components";
import { device, HEADER_HEIGHT_COVERAGE } from "../../../constants";

const NavSection = styled.section`
  display: none;

  @media ${device.maxScreenWidth} {
    display: block;
  }
`;

const Nav = styled.nav`
  position: -webkit-sticky;
  position: sticky;
  top: ${HEADER_HEIGHT_COVERAGE + 5}px;
  align-self: start;

  margin-top: 40px;
`;

const Ul = styled.ul`
  width: 100%;
`;

export const HeaderNavigatorSection = ({ children }) => {
  return (
    <NavSection>
      <Nav>
        <Ul>{children}</Ul>
      </Nav>
    </NavSection>
  );
};
